/*===== Bordes =====*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #0A0A16;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-position: center center;
  background-repeat: no-repeat;
  background-image: url(../img/bgTelXr.png);
}
@media (min-width: 480px) {
  body {
    background-image: url(../img/bgTabletXr.png);
  }
}
@media (min-width: 769px) {
  body {
    background-image: url(../img/bgDesktop.png);
  }
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

footer {
  margin-top: auto;
}

.contenido-footer {
  background-color: #0A0A16;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contenido-footer img {
  margin-left: 2rem;
  width: 2rem;
}
.contenido-footer h2 {
  font-size: 1.5rem;
}
@media (min-width: 769px) {
  .contenido-footer h2 {
    font-size: 2.5rem;
  }
}
.contenido-footer i {
  font-size: 2rem;
}
.contenido-footer i:first-of-type {
  padding-top: 2rem;
}
.contenido-footer .icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 2rem;
}
@media (min-width: 769px) {
  .contenido-footer .icons {
    flex-direction: row;
  }
}
.contenido-footer .icons i {
  margin-right: 2rem;
}
.contenido-footer .icons i:hover {
  color: #9B5B90;
}

.barra {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
}
.barra .separador {
  color: #fff;
}
.barra img {
  margin: 0 auto;
  padding-bottom: 2rem;
  width: 20rem;
}
@media (min-width: 769px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  padding-bottom: 2rem;
}
.navegacion span {
  display: none;
}
@media (min-width: 769px) {
  .navegacion span {
    margin: 2rem;
    visibility: visible;
    user-select: none;
    display: inline;
  }
}
@media (min-width: 769px) {
  .navegacion {
    display: flex;
    align-items: center;
    padding-bottom: 2rem;
  }
}
.navegacion a {
  color: #fff;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 769px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:first-of-type {
    margin-left: 2rem;
  }
}
.navegacion a:hover {
  color: #9B5B90;
}

.btn {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 60px;
  background-color: var(--light-theme);
  cursor: pointer;
  font-size: 24px;
  color: var(--color);
  transition: all 0.3s;
  position: relative;
  text-align: center;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0 6px 30px -10px #cccccc;
}
.btn a {
  color: #fff;
}
.btn a:hover {
  color: #9B5B90;
}
.btn:hover {
  transform: translateX(5px) translateY(-7px);
}

.shop-now {
  position: relative;
}
.shop-now .santa-icon {
  position: absolute;
  width: 30px;
  height: 30px;
  top: -17px;
  right: -20px;
  transform: rotate(14deg);
}
.shop-now .santa-icon img {
  width: 100%;
  height: 100%;
}

.snowflake-grid {
  display: inline-grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 50px;
  gap: 5px;
  width: 100px;
  transform: scale(0.4);
}
.snowflake-grid div {
  border-radius: 5px;
}
.snowflake-grid .snowflake-item {
  position: relative;
}

.to-left {
  position: absolute;
  top: -4px;
  left: -35px;
  animation: swing 3s infinite;
}
.to-left div {
  animation: flash 3s infinite;
}

.to-right {
  position: absolute;
  top: -25px;
  right: -35px;
  animation: swing 2.5s infinite;
}
.to-right div {
  animation: flash 2.5s infinite;
}

.border-left {
  border-left: 4px solid #e6dada;
}

.border-right {
  border-right: 4px solid #e6dada;
}

.border-bottom {
  border-bottom: 4px solid #e6dada;
}

.border-top {
  border-top: 4px solid #e6dada;
}

.sub-items {
  height: 28px;
  width: 28px;
}

.m-w-15 {
  width: 15px;
}

.m-h-15 {
  height: 15px;
}

.r-270 {
  transform: rotate(270deg);
}

.r-180 {
  transform: rotate(180deg);
}

.r-90 {
  transform: rotate(90deg);
}

.pull-down {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.pull-down-right {
  position: absolute;
  bottom: 5px;
  left: 5px;
}

.pull-right {
  position: absolute;
  right: 5px;
  top: 5px;
}

.pull-left {
  position: absolute;
  left: 5px;
  top: 5px;
}

.m-3 {
  margin: 3px;
}

@keyframes swing {
  50% {
    transform: rotateZ(10deg) scale(0.4);
  }
}
@keyframes flash {
  50% {
    border-color: #485563;
  }
}
.contenido-desarrolloWeb {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contenido-desarrolloWeb h2 {
  color: #9B5B90;
  font-weight: 700;
}
.contenido-desarrolloWeb ul .contenido-titulo_li {
  list-style: none;
  text-align: center;
  margin: 0.6rem 0;
  font-weight: 700;
  border-top: 1px solid rgba(110, 110, 110, 0.1);
  border-bottom: 1px solid rgba(110, 110, 110, 0.1);
}

.plan {
  margin-left: 3rem;
  margin-right: 3rem;
  margin-bottom: 12rem;
  width: 100%;
  max-width: 50rem;
  padding: 5rem;
  background-color: #121228;
  box-shadow: 0 1.4rem 8rem rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  border-radius: 0.8rem;
}
.plan h1, .plan p, .plan a, .plan span {
  color: #bbbbbb;
}
.plan:first-of-type {
  margin-top: 20rem;
}
@media (min-width: 769px) {
  .plan {
    max-width: 80rem;
  }
}
@media (min-width: 1024px) {
  .plan {
    max-width: 98rem;
  }
}
.plan .plan-img {
  min-width: 35rem;
  max-width: 35rem;
  height: 30rem;
  transform: translate(0, -8rem);
  position: relative;
}
@media (min-width: 769px) {
  .plan .plan-img {
    transform: translateX(-8rem);
  }
}
.plan .plan-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 0.8rem;
}
.plan .plan-img::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0.5rem 0.5rem 3rem 1px rgba(0, 0, 0, 0.05);
  border-radius: 0.8rem;
}
.plan .plan-fecha span {
  display: block;
  color: #bbbbbb;
  font-size: 1.6rem;
  font-weight: 700;
  margin: 0.5rem 0;
}
.plan .plan-fecha .precio {
  border-top: 1px solid rgba(110, 110, 110, 0.1);
  border-bottom: 1px solid rgba(110, 110, 110, 0.1);
  font-weight: 900;
  font-size: 2rem;
}
.plan .plan-titulo {
  font-size: 2.5rem;
  margin: 1.5rem 0 2rem;
  text-transform: uppercase;
  color: #9B5B90;
  font-weight: 700;
}
.plan .plan-texto {
  margin-bottom: 3rem;
  font-size: 1.4rem;
  color: #bbbbbb;
}
.plan .plan-post_cta {
  display: inline-block;
  padding: 1.5rem 3rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 1.2rem;
  color: #fff;
  background-color: #9B5B90;
  border-radius: 0.8rem;
}
.plan .plan-post_cta:hover {
  background-color: #7b4872;
}

@media screen and (max-width: 1068px) {
  .plan-img {
    min-width: 30rem;
    max-width: 30rem;
  }
}
@media screen and (max-width: 768px) {
  .plan {
    padding: 2.5rem;
    flex-direction: column;
  }

  .plan-img {
    min-width: 100%;
    max-width: 100%;
  }
}
.contenido-inicio h1, .contenido-inicio h2, .contenido-inicio p {
  color: #fff;
}
.contenido-inicio h1 {
  font-size: 5rem;
}
@media (min-width: 769px) {
  .contenido-inicio h1 {
    font-size: 7rem;
  }
}
.contenido-inicio h2 {
  font-size: 4rem;
}
@media (min-width: 769px) {
  .contenido-inicio h2 {
    font-size: 6rem;
  }
}
.contenido-inicio p {
  text-align: center;
}
.contenido-inicio img {
  margin: 0 auto;
  max-width: 30rem;
}

.servicios {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 769px) {
  .servicios {
    flex-direction: row;
    text-align: center;
  }
}
.servicios .servicio img {
  width: 15rem;
}
.servicios .servicio h3 {
  color: #fff;
}

.container {
  width: 100%;
  max-width: 150rem;
  height: auto;
  margin: 0 auto;
  justify-items: center;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3rem;
}
@media (min-width: 769px) {
  .container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}
.container .card {
  background-color: #10101B;
  width: 33rem;
  height: auto;
  padding-bottom: 3rem;
  border-radius: 0.8rem;
  box-shadow: 0 0.2rem 0.2rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
  margin: 2rem;
  text-align: center;
  transition: all 0.25s;
}
.container .card:hover {
  transform: translateY(-1.5rem);
  box-shadow: 0 1.2rem 1.6rem rgba(0, 0, 0, 0.2);
}
.container .card img {
  width: 33rem;
  height: 22rem;
}
.container .card h4 {
  color: #fff;
  font-weight: 700;
}
.container .card p {
  padding: 0 1rem;
  font-size: 1.6rem;
  font-weight: 300;
}
.container .card li {
  text-align: left;
  color: #fff;
}

h2, p {
  color: #fff;
}

h2 {
  text-transform: uppercase;
}

.texto-acercade {
  display: flex;
  flex-direction: column;
  align-content: center;
  background-color: #10101B;
  padding: 2rem;
  border-radius: 3rem;
  text-align: justify;
  align-items: center;
}
.texto-acercade img {
  border-radius: 2rem;
}
@media (min-width: 1024px) {
  .texto-acercade {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

i {
  padding-right: 3rem;
  color: #fff;
}
@media (min-width: 769px) {
  i {
    font-size: 5rem;
  }
}

h1, h3 {
  color: #fff;
}

.contenido-contacto {
  display: flex;
  flex-direction: column;
}
.contenido-contacto i:hover {
  color: #9B5B90;
}
.contenido-contacto .contacta h2, .contenido-contacto .contacta h3 {
  text-align: left;
}
.contenido-contacto .contacta h2 {
  margin-top: 10rem;
  margin-left: 25rem;
}
.contenido-contacto .contacta h3 {
  margin-left: 35rem;
  font-weight: 700;
}
.contenido-contacto .mail-redes h2 {
  margin-top: 12rem;
  font-weight: 700;
}
.contenido-contacto .mail-redes {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  align-items: center;
}

.contenido-nosotros {
  padding: 10rem 0;
}
.contenido-nosotros h1, .contenido-nosotros p {
  color: #bbbbbb;
}
.contenido-nosotros .titulo-nosotros {
  text-transform: uppercase;
  font-size: 3.5rem;
  letter-spacing: 3px;
  margin-right: -3px;
  margin-bottom: 5rem;
  text-align: center;
  color: #bbbbbb;
  position: relative;
}
.contenido-nosotros .titulo-nosotros::after {
  content: "";
  width: 10rem;
  height: 0.8rem;
  background-color: #45e19f;
  position: absolute;
  bottom: -2rem;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2rem;
}
.contenido-nosotros .tarjetas-equipo {
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: column;
}
@media (min-width: 769px) {
  .contenido-nosotros .tarjetas-equipo {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  .contenido-nosotros .tarjetas-equipo {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
    max-width: 100rem;
    margin: 0 auto;
  }
}
.contenido-nosotros .tarjeta-equipo {
  width: 34rem;
  background-color: #0d0d1d;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2rem auto;
  box-shadow: 0.5rem 0.5rem 3rem rgba(0, 0, 0, 0.2);
  border-radius: 1.5rem;
}
.contenido-nosotros .tarjeta-equipo h1 {
  font-size: 2.2rem;
  color: #bbbbbb;
  margin: 1.5rem 0;
  font-weight: 900;
}
@media (min-width: 769px) {
  .contenido-nosotros .tarjeta-equipo {
    width: 43rem;
  }
}
.contenido-nosotros .tarjeta-equipo .card-imgbg {
  width: 100%;
  height: 26rem;
  object-fit: cover;
  clip-path: polygon(0 1%, 100% 0%, 100% 55%, 0% 100%);
  border-radius: 1.5rem;
}
.contenido-nosotros .card-pbg {
  width: 15rem;
  height: 15rem;
  object-fit: cover;
  border-radius: 50%;
  margin-top: -11rem;
  z-index: 999;
