@use '../base/variables' as v;
@use '../base/mixins' as m;

footer{
  margin-top: auto;
}
.contenido-footer {
  background-color: v.$background;
  img {
    margin-left: 2rem;
    width: 2rem;
  }

  display: flex;
  justify-content: space-between;
  align-items: center;

  h2 {
    font-size: 1.5rem;
    @include m.tablet{
      font-size: 2.5rem;
    }
  }


  i {
    font-size: 2rem;
    &:first-of-type{
      padding-top: 2rem;
    }
  }

  .icons {
    display: flex;
    flex-direction: column;
    @include m.tablet{
      flex-direction: row;
    }
    align-items: center;
    i {
      margin-right: 2rem;
      &:hover{
        color: v.$hover;
      }
    }

    margin-right: 2rem;
  }
}